.novelties {
  padding-top: 130px;
  padding-bottom: 130px;
  position: relative;

  h2 {
    @include font(34,22);
    font-family: "CormorantGaramond", "Times New Roman", serif;
    font-weight: 700;
    margin: 0 0 35px;
    text-align: center;
  }
}

.novelties__link {
  line-height: 22px;
  margin: 0 0 60px;
  display: block;
  text-decoration: none;
  text-align: center;
  position: relative;

  &::after {
    @include pseudo-elem;
    height: 1px;
    background-color: #a87b62;
    width: 80px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.novelties__slider-container {
  overflow: hidden;
}

.novelties__slider {
  @include list-reset;
  display: flex;
  margin-bottom: 60px;
}

.novelties__slide {
  display: flex;
  flex-shrink: 0;

  a {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    margin-right: 30px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.novelties__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #1d1613;

  h3 {
    @include font(16,12);
    font-weight: 500;
    margin: 0 0 15px;
  }

  b {
    @include font(14,10);
    font-weight: 700;
  }
}

.novelties__slide-image {
  display: block;
  margin-bottom: 25px;
}

.novelties__slider-pagination {
  display: block;
  text-align: center;
  line-height: 12px;
  color: #9b9997;

  .swiper-pagination-bullet {
    margin-right: 15px;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }
  }

  .swiper-pagination-bullet-active {
    color: #1d1613;
  }
}

.novelties__slider-btn {
  position: absolute;
  top: 158px;

  &--prev {
    left: 98px;
  }

  &--next {
    right: 98px;
  }
}
