.blog {
  padding-bottom: 130px;

  h2 {
    @include font(34,22);
    font-family: "CormorantGaramond", "Times New Roman", serif;
    font-weight: 700;
    margin: 0 0 35px;
    text-align: center;
  }
}

.blog__link {
  line-height: 22px;
  margin: 0 0 60px;
  display: block;
  text-decoration: none;
  text-align: center;
  position: relative;

  &::after {
    @include pseudo-elem;
    height: 1px;
    background-color: #a87b62;
    width: 64px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.blog__list {
  @include list-reset;
  display: grid;
  grid-template-columns: 570px 570px;
  grid-template-rows: 300px 300px;
  column-gap: 30px;
  row-gap: 30px;
}

.blog__item {
  display: flex;

  &--big {
    position: relative;
    grid-column-start: 2;
    grid-row: 1/3;
  }
}

.blog__image {
  display: block;
}

.blog__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0eae4;

  &--another {
    position: absolute;
  }

  span {
    @include font(14,10);
    color: #9b9997;
    padding-top: 71px;
    margin-bottom: 25px;
  }

  h3 {
    @include font(26,34);
    font-family: "CormorantGaramond", "Times New Roman", serif;
    font-weight: 600;
    color: #000000;
    margin: 0 0 50px;
    text-align: center;
    padding: 0 56px;
  }

  a {
    text-decoration: none;
    line-height: 21px;
  }
}
