html {
  box-sizing: border-box;
  height: 100%;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif;
  color: #1d1613;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
}

.body--locked {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 98px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.page-header {
  position: relative;
  width: 100%;
}
.page-header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #f6f5f4;
  bottom: 60px;
}
@media (max-width: 1023px) {
  .page-header::after {
    display: none;
  }
}

.page-header__user-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 19px;
}
@media (max-width: 1023px) {
  .page-header__user-nav {
    padding-top: 30px;
    padding-bottom: 30px;
    min-height: 126px;
  }
}
@media (max-width: 767px) {
  .page-header__user-nav {
    padding-top: 20px;
    padding-bottom: 20px;
    min-height: 92px;
  }
}

.page-header__site-nav {
  position: relative;
  padding-top: 18px;
  padding-bottom: 19px;
}

.page-header--nav-open {
  font-size: 24px;
  line-height: 17px;
  color: #ffffff;
  position: absolute;
  z-index: 2;
  background-color: #a87b62;
}
.page-header--nav-open .user-nav__toggle svg {
  stroke: #ffffff;
}
@media (max-width: 767px) {
  .page-header--nav-open .user-nav__toggle {
    bottom: 630px;
  }
}
.page-header--nav-open .user-nav__logo-desktop {
  fill: #ffffff;
}
.page-header--nav-open .user-nav__logo-mobile {
  fill: #ffffff;
}
.page-header--nav-open .user-nav {
  min-height: 136px;
  padding-top: 30px;
  padding-bottom: 40px;
  display: flex;
}
@media (max-width: 767px) {
  .page-header--nav-open .user-nav {
    min-height: 102px;
    padding-top: 20px;
    padding-bottom: 30px;
  }
}
.page-header--nav-open .user-nav__search {
  display: block;
}
.page-header--nav-open .user-nav__cart-link svg {
  fill: #a87b62;
  stroke: #ffffff;
}
.page-header--nav-open .site-nav {
  margin-top: 95px;
  padding-bottom: 80px;
}
@media (max-width: 767px) {
  .page-header--nav-open .site-nav {
    margin-top: 85px;
    padding-bottom: 50px;
  }
}
.page-header--nav-open .site-nav__list {
  display: flex;
}
.page-header--nav-open .shop-nav__list {
  display: flex;
}
.page-header--nav-open .shop-nav__account-link {
  display: flex;
}

.user-nav {
  font-size: 14px;
  line-height: 21px;
}

.user-nav__search svg {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .user-nav__search svg {
    fill: #ffffff;
    margin-right: 0;
    position: absolute;
    left: 12px;
  }
}
@media (max-width: 1023px) {
  .user-nav__search {
    width: 88%;
    top: 136px;
    position: absolute;
    display: none;
  }
}
@media (max-width: 1023px) and (max-width: 767px) {
  .user-nav__search {
    top: 102px;
  }
}
.user-nav__search label {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .user-nav__search label {
    position: relative;
  }
}
.user-nav__search input[type=search] {
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  color: #9b9997;
  opacity: 0.47;
  width: 140px;
  padding: 0;
  outline: none;
  border: 0;
}
.user-nav__search input[type=search]::-webkit-search-decoration {
  display: none;
}
.user-nav__search input[type=search]::-webkit-search-cancel-button {
  display: none;
}
.user-nav__search input[type=search]::-webkit-search-results-button {
  display: none;
}
.user-nav__search input[type=search]::-webkit-search-results-decoration {
  display: none;
}
@media (max-width: 1023px) {
  .user-nav__search input[type=search] {
    width: 100%;
    border-radius: 21px;
    background-color: rgba(255, 255, 255, 0.15);
    min-height: 40px;
    opacity: 1;
    color: #ffffff;
    padding-left: 30px;
  }
  .user-nav__search input[type=search]::placeholder {
    color: transparent;
    margin-left: 9px;
  }
}
.user-nav__search input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}
.user-nav__search input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

.user-nav__toggle {
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  display: none;
}
@media (max-width: 1023px) {
  .user-nav__toggle {
    display: flex;
  }
}

.user-nav__logo {
  display: flex;
  margin: 0 auto;
}

.user-nav__logo-mobile {
  display: none;
}
@media (max-width: 767px) {
  .user-nav__logo-mobile {
    display: block;
  }
}

@media (max-width: 767px) {
  .user-nav__logo-desktop {
    display: none;
  }
}

.user-nav__account-link {
  text-decoration: none;
  margin-right: 35px;
}
@media (max-width: 1023px) {
  .user-nav__account-link {
    display: none;
  }
}

.user-nav__cart-link {
  text-decoration: none;
}
@media (max-width: 1023px) {
  .user-nav__cart-link {
    font-size: 0;
    display: flex;
  }
}
.user-nav__cart-link svg {
  display: none;
}
@media (max-width: 1023px) {
  .user-nav__cart-link svg {
    display: block;
  }
}

@media (max-width: 1023px) {
  .site-nav {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  .site-nav__list {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 55px;
    display: none;
  }
}
@media (max-width: 767px) {
  .site-nav__list {
    margin-bottom: 45px;
  }
}

.site-nav__item {
  margin-right: 35px;
}
.site-nav__item:first-child {
  margin-right: 34px;
}
@media (max-width: 1023px) {
  .site-nav__item:first-child {
    margin-right: 0;
  }
}
.site-nav__item:nth-child(4) {
  margin-right: 34px;
}
@media (max-width: 1023px) {
  .site-nav__item:nth-child(4) {
    margin-right: 0;
  }
}
.site-nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .site-nav__item:last-child {
    margin-bottom: 0;
  }
}
.site-nav__item a {
  text-decoration: none;
}
@media (max-width: 1023px) {
  .site-nav__item {
    margin-right: 0;
    margin-bottom: 40px;
  }
}

.shop-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
  line-height: 13px;
  color: rgba(255, 255, 255, 0.75);
  flex-direction: column;
  flex-wrap: wrap;
  margin-bottom: 55px;
  display: none;
}
@media (max-width: 767px) {
  .shop-nav__list {
    margin-bottom: 45px;
  }
}

.shop-nav__item {
  margin-bottom: 30px;
}
.shop-nav__item:last-child {
  margin-bottom: 0;
}
.shop-nav__item a {
  text-decoration: none;
}

.shop-nav__account-link {
  font-size: 18px;
  line-height: 13px;
  text-decoration: none;
  display: none;
}

.intro {
  font-family: "CormorantGaramond", "Times New Roman", serif;
  color: #ffffff;
  min-height: 463px;
  display: flex;
  align-items: center;
