.novelties {
  padding-top: 130px;
  padding-bottom: 130px;
  position: relative;
}
.novelties h2 {
  font-size: 34px;
  line-height: 22px;
  font-family: "CormorantGaramond", "Times New Roman", serif;
  font-weight: 700;
  margin: 0 0 35px;
  text-align: center;
}

.novelties__link {
  line-height: 22px;
  margin: 0 0 60px;
  display: block;
  text-decoration: none;
  text-align: center;
  position: relative;
}
.novelties__link::after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: #a87b62;
  width: 80px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.novelties__slider-container {
  overflow: hidden;
}

.novelties__slider {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 60px;
}

.novelties__slide {
  display: flex;
  flex-shrink: 0;
}
.novelties__slide a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  margin-right: 30px;
}
.novelties__slide a:last-child {
  margin-right: 0;
}

.novelties__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #1d1613;
}
.novelties__info h3 {
  font-size: 16px;
  line-height: 12px;
  font-weight: 500;
  margin: 0 0 15px;
}
.novelties__info b {
  font-size: 14px;
  line-height: 10px;
  font-weight: 700;
}

.novelties__slide-image {
  display: block;
  margin-bottom: 25px;
}

.novelties__slider-pagination {
  display: block;
  text-align: center;
  line-height: 12px;
  color: #9b9997;
}
.novelties__slider-pagination .swiper-pagination-bullet {
  margin-right: 15px;
  cursor: pointer;
}
.novelties__slider-pagination .swiper-pagination-bullet:last-child {
  margin-right: 0;
}
.novelties__slider-pagination .swiper-pagination-bullet-active {
  color: #1d1613;
}

.novelties__slider-btn {
  position: absolute;
  top: 158px;
}
.novelties__slider-btn--prev {
  left: 98px;
}
.novelties__slider-btn--next {
  right: 98px;
}

.blog {
