  height: 1px;
  background-color: #a87b62;
  width: 64px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.blog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 570px 570px;
  grid-template-rows: 300px 300px;
  column-gap: 30px;
  row-gap: 30px;
}

.blog__item {
  display: flex;
}
.blog__item--big {
  position: relative;
  grid-column-start: 2;
  grid-row: 1/3;
}

.blog__image {
  display: block;
}

.blog__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0eae4;
}
.blog__container--another {
  position: absolute;
}
.blog__container span {
  font-size: 14px;
  line-height: 10px;
  color: #9b9997;
  padding-top: 71px;
  margin-bottom: 25px;
}
.blog__container h3 {
  font-size: 26px;
  line-height: 34px;
  font-family: "CormorantGaramond", "Times New Roman", serif;
  font-weight: 600;
  color: #000000;
  margin: 0 0 50px;
  text-align: center;
  padding: 0 56px;
}
.blog__container a {
  text-decoration: none;
  line-height: 21px;
}